import React, { Fragment, useState, useEffect } from 'react';


const shouldShowUrl = (scrollPosition, top, height) => {
    return (scrollPosition + 200 > top) && ((top + height) > (scrollPosition - window.innerHeight - 500))
}

const GalleryImage = ({ key, index, left, top, photo, scrollPosition }) => {
    const { width, height, project, name, ...props } = photo;
    const [showUrl, setShowUrl] = useState(shouldShowUrl(scrollPosition, top, height));

    useEffect(() => {
        if (shouldShowUrl(scrollPosition, top, height)) {
            setShowUrl(true);
        }
    }, [top, scrollPosition, height])

    const src = `http://demkina.me/images/compressed/${project.id}/${name}`;
    return <Fragment>
        <div style={{ top, left, width: width + 4, height: height + 4 }} className="gallery-image">
            {showUrl ? <img
                className="img"
                id={name}
                {...props}
                alt={`Изображение из серии "${project.title}"`}
                {...(showUrl && {src})}
            /> : <div className="img placeholder"/>}
        </div>
    </Fragment>
}

export default GalleryImage;
