import { photos } from './api/photos';
import { projects } from './api/projects';


export const getImage = inp => {
    const image = typeof(inp) === 'string' ? photos.filter(i => i.name === inp)[0] : inp;
    return image ? { ...image, project: projects[image.project], src: '', key: image.project + image.name  } : null
}

export const getProject = name => {
    const project = projects[name];
    return { ...project, images: photos.filter(i => i.project === name) }
}

export const columns = containerWidth => {
	let columns = 3;
	if (containerWidth >= 500) columns = 2;
	if (containerWidth >= 900) columns = 3;
	if (containerWidth >= 1024) columns = 4;
	if (containerWidth >= 1440) columns = 5;
	if (containerWidth >= 1920) columns = 6;
	return columns;
}
