import React, { useEffect, useState, useCallback, useMemo, useRef } from 'react';
import Gallery from 'react-photo-gallery';
import { photos } from '../api/photos';
import { getImage, getProject, columns } from '../utils';
import GalleryImage from './GalleryImage';
import ImageOverlay from './ImageOverlay';
import Project from './Project';

const getImagesForPage = (project, onClick) => {
    if (!project) {
        return photos
            .map(photo => getImage(photo))
            .map(photo => ({ ...photo, onClick: () => onClick(photo.name) }))
            .map(a => [Math.random(), a])
            .sort((a, b) => a[0] - b[0])
            .map(a => a[1]);
    } else {
        return photos
            .filter(photo => photo.project === project)
            .map(photo => getImage(photo))
            .map(photo => ({ ...photo, onClick: () => onClick(photo.name) }));
    }
}

const IndexPage = ({ history, location, match }) => {

    const lastScrollCheckRef = useRef(-10000);

    const onClick = useCallback(name => history.push(`#${name}`), [history]);
    const images = useMemo(() => getImagesForPage(match.params.project, onClick), [match.params.project, onClick]);
    const imagesNames = useMemo(() => images.map(image => image.name), [images]);

    const [showImage, setShowImage] = useState(location.hash.length > 1);
    const [scrollPosition, setScrollPosition] = useState(0)
    useEffect(() => {
        setShowImage(Boolean(location.hash.slice(1)));
    }, [location.hash]) 

    const getCurrIndex = name => imagesNames.indexOf(name);
    const getNextIndex = name => Math.min(getCurrIndex(name)+1, imagesNames.length-1);
    const getPrevIndex = name => Math.max(getCurrIndex(name)-1, 0);

    const nextImage = () => history.push(`#${imagesNames[getNextIndex(location.hash.slice(1))]}`);
    const prevImage = () => history.push(`#${imagesNames[getPrevIndex(location.hash.slice(1))]}`);

    const onScroll = useCallback(() => {
        const now = window.performance.now();
        if (now - lastScrollCheckRef.current > 100) {
            lastScrollCheckRef.current = now;
            setScrollPosition(window.pageYOffset + window.innerHeight)
        }
    }, [])

    useEffect(() => {
        document.addEventListener('scroll', onScroll);
        onScroll();
        return () => document.removeEventListener('scroll', onScroll);
    }, [onScroll])

    useEffect(() => {
        onScroll();
    }, [onScroll, location.pathname])

    return <>
        { match.params.project  && <Project {...getProject(match.params.project)}/> }
        <Gallery photos={images} direction={'column'} renderImage={props => <GalleryImage {...props} scrollPosition={scrollPosition}/>} columns={columns} />
        { showImage ? <ImageOverlay 
            prevImage={() => prevImage()}
            nextImage={() => nextImage()}
            stats={`${getCurrIndex(location.hash.slice(1))+1} / ${imagesNames.length}`}
            image={getImage(location.hash.slice(1))}
            currentIndex={getCurrIndex(location.hash.slice(1))}
            images={images} 
            close={() => history.push('#')}/> : null }
    </>
}

export default IndexPage;
