import React from "react";


const Project = ({ id, thumbnail, title, description, images }) => {
    return <header className="project">
        <div className="img-wrapper">
            <h1 className="mobile-title">{title}</h1>
            <img src={`http://demkina.me/images/original/${id}/${thumbnail || images[0].name}`} alt=""/>
        </div>
        <div className="img-wrapper">
            <div className="text    ">
                <h1 className="desktop-title">{title}</h1>                
                <p>{description}</p>
                <button 
                    className="desktop-title btn" 
                    onClick={() => document.getElementById(images[0].name).scrollIntoView({ behavior: 'smooth' })}
                >
                    Все работы
                </button>
            </div>
        </div>
    </header>
}

export default Project;
