import React, { useRef, useState, useEffect, useCallback } from 'react';
import { Link } from 'react-router-dom';


const Img = ({ name, project, i, show }) => {
    const src = show ? `http://demkina.me/images/original/${project.id}/${name}`: ''
    return <div className="slide">
        <img src={src} alt=""/>
    </div>
}

const unify = e => e.changedTouches ? e.changedTouches[0] : e;

const ImageOverlay = ({ image, close, images, currentIndex, prevImage, nextImage, stats }) => {
    const [cIndex, setCIndex] = useState(1);
    const isMoving = useRef(false);
    const startMousePosition = useRef(null);
    const containerRef = useRef(null)
    const [useAnimation, setUseAnimation] = useState(true);
    const timerRef = useRef(null);

    useEffect(() => {
        setCIndex(Math.min(currentIndex,1));
    }, [currentIndex])

    const keys = useCallback(event => {
        if (event.code === "Escape") close();
        if (event.code === "ArrowRight" && ((images.length - 1) !== currentIndex)) {
            setUseAnimation(true);
            setCIndex(currentIndex === 0 ? 1 : 2);
            timerRef.current && clearTimeout(timerRef.current);
            setTimeout(() => {
                setUseAnimation(false);
                nextImage();
            }, 500);
        }
        if (event.code === "ArrowLeft" && 0 !== currentIndex) {
            setUseAnimation(true);
            setCIndex(0);
            timerRef.current && clearTimeout(timerRef.current);
            setTimeout(() => {
                setUseAnimation(false);
                prevImage();
            }, 500);
        }            
    }, [currentIndex, images.length, nextImage, prevImage, close])

    useEffect(() => {
        document.body.style.overflow = 'hidden'
        document.addEventListener('keydown', keys)
        return () => {
            document.body.style.overflow = null
            document.removeEventListener('keydown', keys)
        }
    }, [keys])

    const calcNextI = v => {
        if (v === 2) {
            return images.length - 1 === currentIndex ? 1 : 2
        } else {
            return v
        }
    }

    const onDragStart = (event) => {
        isMoving.current = true;
        startMousePosition.current = {
            x: unify(event).clientX,
            y: unify(event).clientY
        }
    }
    const onDragEnd = (event) => {
        let i = cIndex
        if (isMoving.current && Math.abs(Math.round(unify(event).clientY - startMousePosition.current.y)) > 100) {
            event.preventDefault();
            close();
            return
        }
        if(isMoving.current && (startMousePosition.current || startMousePosition.current.x === 0)) {
            let dx = unify(event).clientX - startMousePosition.current.x, s = Math.sign(dx);

            if((i > 0 || s < 0) && (i < 3 - 1 || s > 0) && (Math.abs(dx) > 100)) {    
                const nextI = calcNextI(i - s);
                const slideF = cIndex > nextI ? prevImage : nextImage;
                setUseAnimation(true);
                setCIndex(nextI);
                timerRef.current && clearTimeout(timerRef.current);
                setTimeout(() => {
                    setUseAnimation(false);
                    slideF();
                }, 500);
            }
            startMousePosition.current = null
            isMoving.current = false;
        }
        containerRef.current.style.setProperty('--tx', '0px')
        containerRef.current.style.setProperty('--ty', '0px')
        containerRef.current.style.setProperty('--y', '0')
    }
    const onMove = (event) => {
        event.preventDefault();
        if (isMoving.current && (startMousePosition.current || startMousePosition.current.y === 0) && Math.abs(Math.round(unify(event).clientY - startMousePosition.current.y)) > 20) {
            containerRef.current.style.setProperty('--ty', `${Math.round(unify(event).clientY - startMousePosition.current.y)}px`)
            containerRef.current.style.setProperty('--y', `${Math.abs(Math.round(unify(event).clientY - startMousePosition.current.y))}`)
        }
        if (isMoving.current && (startMousePosition.current || startMousePosition.current.x === 0) && Math.abs(Math.round(unify(event).clientX - startMousePosition.current.x)) > 20) {
            containerRef.current.style.setProperty('--tx', `${Math.round(unify(event).clientX - startMousePosition.current.x)}px`)
        }
    }

    return image ? 
        <div className="overlay-wrapper">
            <header>
                <Link to={`/projects/${image.project.id}`}>{image.project.title}</Link>
                <span>{stats}</span>
                <span style={{ textAlign: "end" }}>
                    <button onClick={close}>&times;</button>
                </span>
            </header>
            <article 
                className={useAnimation ? "image-overlay use-animation" : "image-overlay"}
                ref={containerRef} 
                style={{ '--n': 3, '--i': cIndex }}
                onMouseDown={onDragStart}
                onTouchStart={onDragStart}
                onMouseUp={onDragEnd}
                onTouchEnd={onDragEnd}
                onMouseMove={onMove}
                onTouchMove={onMove}
            >
                {images.slice(Math.max(0, currentIndex-1), Math.min(images.length, currentIndex+2)).map((image, i) => {
                    return <Img name={image.name} project={image.project} key={image.key+'original'} show={true} i={i} />    
                })}
            </article> 
            <div className="arrow arrow-left" onClick={() => keys({ code: 'ArrowLeft' })}/>
            <div className="arrow arrow-right" onClick={() => keys({ code: 'ArrowRight' })}/>
        </div>
        : null
}

export default ImageOverlay;
